import React from 'react'
import styled from 'styled-components'
import { Text, Flex, IconButton, ArrowBackIcon, useModal, CogIcon } from '@pancakeswap/uikit'
import { Link } from 'react-router-dom'
import { useExpertModeManager } from 'state/user/hooks'
import GlobalSettings from 'components/Menu/GlobalSettings'
import Transactions from './Transactions'
import ManualButton from './ManualButton'
import QuestionHelper from '../QuestionHelper'
import SettingsModal from '../Menu/GlobalSettings/SettingsModal'

interface Props {
  title: string
  subtitle: string
  helper?: string
  backTo?: string
  noConfig?: boolean
  settingsIcon?: boolean
}

const AppHeaderContainer = styled(Flex)`
  align-items: center;
  justify-content: space-between;
  padding: 24px;
  width: 100%;
  border-bottom: 1px solid ${({ theme }) => theme.colors.cardBorder};
  // border: none;
  // background: linear-gradient(180deg, #191961 0%, #0F0F37 100%);
  background-color: #C70101;
`

const AppHeader: React.FC<Props> = ({ title, subtitle, helper, backTo, noConfig = false, settingsIcon }) => {
  const [expertMode] = useExpertModeManager()
  const [onPresentSettingsModal] = useModal(<SettingsModal />)
  const getSettingsIcon = () => {
    return (
      <IconButton onClick={onPresentSettingsModal} variant="text" scale="sm" mr="8px" id="open-settings-dialog-button">
        <CogIcon height={22} width={22} color="white" />
      </IconButton>
    )
  }

  return (
    <AppHeaderContainer>
      <Flex alignItems="center" mr={noConfig ? 0 : '16px'}>
        {backTo && (
          <IconButton as={Link} to={backTo}>
            <ArrowBackIcon width="32px" />
          </IconButton>
        )}
        <Flex flexDirection="column">
          <Text color="white" fontSize="24px">
            {title}
          </Text>
          <Flex alignItems="center">
            {helper && <QuestionHelper text={helper} mr="4px" />}
            <Text color="#A0A0B3" fontSize="14px">
              {subtitle}
            </Text>
          </Flex>
        </Flex>
      </Flex>
      {!noConfig && (
        <Flex alignItems="center">
          {/* <NotificationDot show={expertMode}>
            <GlobalSettings />
          </NotificationDot> */}
          {
            // settingsIcon ?
            //   getSettingsIcon()
            //   :
              <>
                {getSettingsIcon()}
                <Transactions />
              </>
          }
        </Flex>
      )}
    </AppHeaderContainer>
  )
}

export default AppHeader
